/* CartNew.css */

/* Default (light theme) styles */
.cart-container {
   
    padding: 10px;
   
    color: black;
}

.cart-container.dark-theme {
    border-color: #444;
    background-color: #333;
    color: white;
}

.cart-items-title p {
    color: inherit; /* Use the inherited text color from the parent */
}

.cart-items-item {
    border-bottom: 1px solid #ccc;
    padding: 10px 0;
}

.cart-items-item p {
    color: inherit; /* Use the inherited text color from the parent */
}

.cart-bottom {
    border-top: 1px solid #ccc;
    padding-top: 10px;
}

.cart-bottom p {
    color: inherit; /* Use the inherited text color from the parent */
}

.cart-total-details p {
    color: inherit; /* Use the inherited text color from the parent */
}

.cart-total-details b {
    color: inherit; /* Use the inherited text color from the parent */
}

button {
    background-color: #555;
    color: white;
}

input {
    background-color: #444;
    color: white;
}

.coupon-error {
    color: red;
}

.cross {
    color: red;
    cursor: pointer;
}
